body{
    background-color: #000;
    color: #fff;
}
html{
    color: #fff;
}
.otraletra{ 
    font-family: Arial, Helvetica, sans-serif !important;
}
.resultadoIncorrecto{
    padding: 2.5rem;
    color: #fff;
    background-color: rgb(194,0,0);
    text-align: center;
    font-weight: bold;
}

.inputs{
    display: flex;
    flex-direction: column;
    flex-wrap: 3rem;
    align-items: center;


    color:#0da6f3 ;
    padding: 2rem;
}
.input{
    border: none;
    color: #0da6f3;
    height: 2.5rem;
    width: 50%;
    margin-bottom: 2rem;
    font-size: 1.9rem;
    text-align: center;
    border-radius: .2rem;
    &::placeholder{
        color: #0da6f3;
        text-align: center;
        font-size: 1.9rem;
    }
    &:hover{
        background-color: #0da6f3;
        color: #fff;
        cursor: pointer;
        &::placeholder{
            color: #fff;
        }
}
}
.button{
    font-weight: 700;
    margin-bottom: 1.5rem;
    width: 55%;
    padding: 1rem;
    text-transform: uppercase;
    color: #0da6f3;
    font-size: 1.7rem;
    border-radius: .2rem;
    border: none;
    cursor: pointer;
    &:hover{
        background-color: #0da6f3;
        color: #fff;
        cursor: pointer;
        &::placeholder{
            color: #fff;
        }
    }
}

.text-aling{
    text-align: center;
}

.resultado{
    color: #fff;
}